<template>
  <!--多平台终端接入开始-->
  <div class="multi-platform wrapper">
    <div class="container clearfix">
      <img src="/static/images/index/phone.png" alt="" class="pull-left multi-platform-phone slide-left"/>
      <div class="pull-right multi-platform-des">
        <h1>GCX Wallet</h1>
        <h5>{{ $t("download.index.part1") }}</h5>
        <p><i></i>{{ $t("download.index.part2") }}</p>
        <p><i></i>{{ $t("download.index.part3") }}</p>
        <p><i></i>{{ $t("download.index.part4") }}</p>
        <div class="code clearfix">
          <qrcode-vue :value="qrcode_Url" size="110" level="H" class="pull-left code-img"></qrcode-vue>
          <div class="pull-left">
            <a :href="androd_link" class="android"><img src="/static/images/index/android.png" alt="">Android</a>
            <a :href="ios_link" class="iphone"><img src="/static/images/index/iphone.png" alt="">Iphone</a>
          </div>
        </div>

      </div>
    </div>
  </div>
  <!--多平台终端接入结束-->
</template>

<script>
  import QrcodeVue from 'qrcode.vue'
  import { getWalletAppDownLoad } from '@/api/index'
  // import scrollReveal from 'scrollreveal'
  export default {
    components: {
      QrcodeVue
    },
    data() {
      return {
        ios_link: '',
        androd_link: '',
        qrcode_Url: ''
      }
    },
    mounted() {
      this.initAppLink()
      // const sr = scrollReveal()

      // sr.reveal('.fade-in', { duration: 500, reset: true })
      // sr.reveal('.scale-in', { scale: 0.5, duration: 500, reset: true })
      // sr.reveal('.slide-left', { origin: 'left', distance: '200px', duration: 1000, reset: false })
      // sr.reveal('.slide-right', { origin: 'right', distance: '200px', duration: 500, reset: true })
    },
    methods: {
      initAppLink() {
        getWalletAppDownLoad().then(res => {
          if (res.status !== 1000) {
            return false
          }
          this.ios_link = res.data.ios_link
          this.androd_link = res.data.androd_link

          if (this.$i18n.locale === 'zn') {
            this.qrcode_Url = res.data.zh_url
          } else if (this.$i18n.locale === 'en') {
            this.qrcode_Url = res.data.en_url
          } else {
            this.qrcode_Url = res.data.zhtw_url
          }
        }).catch(() => {
        })
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang="less">
  .multi-platform{
    width: 100%;
    // background: url(/static/images/index/bannerbj2.png) no-repeat;
    // background-size: 100% 100%;
    color: #959aa7;
    min-height: 580px;
    min-width: 1200px;
    h1{
      font-size: 40px;
      font-weight: normal;
      color: #31415d;
    }
    h5{
      font-size: 18px;
      font-weight: normal;
      margin: 25px 0;
      color: #6a7385;
    }
    p{
      font-size: 14px;
      margin-bottom: 10px;
      padding-left: 17px;
      position: relative;
      i{
        width: 8px;
        height: 8px;
        background: #959aa7;
        border-radius: 50%;
        display: inline-block;
        margin-right: 10px;
        position: absolute;
        left: 0;
        top: 6px;
      }
    }
    .multi-platform-phone{
      margin-top: 75px;
    }
    .multi-platform-des{
      margin-top: 90px;
      width: 680px;
      text-align: left;
      .code{
        margin-top: 40px;
        margin-left: 20px;
        >img{
          width: 110px;
          height: 110px;
        }
        .code-img{
          border: 4px solid white; background: white; width: 110px; height: 110px;
        }
        a{
          border-radius: 20px;
          display: block;
          margin-left: 20px;
          width: 160px;
          line-height: 42px;
          img{
            vertical-align: middle;
            margin: 0 15px 0 25px;
          }
          &.android{
            color: white;
            background: #01df9e;
            margin-bottom: 28px;
          }
          &.iphone{
            color: #31415d;
            background: #fff;
          }
        }
      }
    }
  }
</style>



